import discord from './discord-logo.png';
import dotboll from './dotboll.png'

function Projetos() {
  return (
    <div className="Projetos-page">
        <Project 
            name="Bot para Discord" 
            img={discord} 
            url="https://github.com/TheSmileyDroid/SmileyDroidLoco"

        />
        <Project 
            name="Dotboll" 
            img={dotboll}
            url="https://dotboll.herokuapp.com"
        />
    </div>
  )
}

function Project(props) {
  return (
    <div className="Projeto">
      <a href={props.url}>
        <img src={props.img}></img>
      </a>
      <h3>{props.name}</h3>
    </div>
  )
}

export default Projetos